*{
    padding:0px;
    margin: 0px;
}
.navbar{
    display: flex;
    flex-direction: row;
    background-color: aqua;
    height: 10vh;
    padding-top: 10px;
    justify-content: space-between;
    padding-right: 50px;
    border-bottom: 1px solid blue;
    position: sticky;
}
.listl li {
    padding: 0px;
    margin: 0px;
    text-decoration: none;
    list-style: none;
    margin-right: 50px;
    margin-top: 10px;
    text-transform: capitalize;
    font-size: 20px;
    font-weight: 600;
    text-align: center;
    

}
.listl{
    display: flex;
    flex-direction: row;
